.form__wrapper {
  display: flex;
  align-items: center;
  gap: 30px;

  @include vp-1199 {
    flex-direction: column;
    gap: 11px;
  }

  @include vp-767 {
    width: 290px;
    margin: 0 auto;
    gap: 8px;
  }
}

.form__input {
  @include vp-767 {
    width: 100%;
  }

  input {
    padding: 19px 30px;
    width: 360px;
    min-height: 56px;
    border: none;

    @include vp-1199 {
      width: 270px;
      min-height: 50px;
      padding: 15px;
    }

    @include vp-767 {
      width: 290px;
    }

    &::placeholder {
      font-size: 16px;
      font-weight: 400;
      line-height: 16px;
      color: $color-elephant;
      opacity: 0.5;
    }

    &:hover {
      outline: 1px solid $color-navy-blue;

      &::placeholder {
        color: $color-navy-blue;
        opacity: 1;
      }
    }

    &:focus,
    &:active {
      outline: 1px solid $color-resolution-blue;

      &::placeholder {
        color: $color-transparent;
      }
    }

    @include disabled {
      cursor: not-allowed;
      pointer-events: none;
      outline: 1px solid $color-navy-blue-10;
      background-color: $color-default-white;

      &::placeholder {
        color: $color-navy-blue;
        opacity: 0.5;
        cursor: not-allowed;
        pointer-events: none;
      }
    }
  }
}

.form__input.is-invalid {
  input {
    outline: 1px solid $color-torch-red;
  }
}

.form__btn {
  @include vp-1199 {
    width: 270px;
  }

  @include vp-767 {
    width: 290px;
  }
}
