.features {
  position: relative;
}

.features__wrapper {
  position: relative;
  z-index: 3;
  padding: 40px 0 81px;

  @include vp-1199 {
    padding: 43px 0 45px;
  }

  @include vp-767 {
    padding: 23px 0 40px;
  }
}

.features__clouds-left {
  position: absolute;
  top: -125px;
  left: 0;
  z-index: 2;
  width: 875px;
  height: 402px;

  @include vp-1199 {
    left: -2px;
    top: 214px;
    width: 753px;
    height: 402px;
  }

  @include vp-767 {
    display: none;
  }
}

.features__clouds-right {
  position: absolute;
  top: 60px;
  right: 0;
  z-index: 2;
  width: 462px;
  height: 263px;

  @include vp-1199 {
    top: auto;
    bottom: 247px;
    width: 555px;
    height: 263px;
  }

  @include vp-767 {
    display: none;
  }
}


.features__title {
  text-align: center;
  margin-bottom: 47px;
  margin-top: 25px;

  @include vp-767 {
    margin-bottom: 19px;

    &::after {
      top: -4px;
    }
  }
}

.features__swiper {
  @include vp-1199 {
    position: relative;
  }
}

.features__list {
  position: relative;
  left: 0;
  padding: 0;
  margin: 0 0 50px;
  list-style: none;

  @include vp-1199 {
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
  }

  @include vp-767 {
    gap: 15px;
  }
}

.features__item {
  padding: 39px 18px;
  height: auto;
  display: flex;
  flex-direction: column;
  background-color: $color-default-white;
  text-align: center;
  align-items: center;
  border: 1px solid $color-navy-blue-10;

  @include vp-1199 {
    flex-basis: 50%;
    max-width: 452px;
    padding: 29px 20px;

    &:last-child {
      max-width: 380px;
    }
  }

  @include vp-1023 {
    max-width: 324px;
    padding: 29px 19px;

    &:last-child {
      min-width: 380px;

      h3 {
        padding: 0 70px;
      }
    }
  }

  @include vp-767 {
    max-width: 100%;
    padding: 19px 10px;
    flex-basis: 91.5%;

    &:last-child {
      min-width: 320px;

      h3 {
        padding: 0 60px;
      }
    }
  }

  svg {
    width: 54px;
    height: 54px;
    padding: 15px;
    outline: 1px solid $color-navy-blue;
    outline-offset: -1px;

    @include vp-1199 {
      width: 48px;
      height: 48px;
      padding: 12px;
    }

    @include vp-767 {
      width: 42px;
      height: 42px;
      padding: 9px;
    }
  }

  h3 {
    font-size: 18px;
    font-weight: 600;
    line-height: 18px;
    margin: 30px 0 15px;

    @include vp-1199 {
      font-size: 16px;
      line-height: 16px;
      margin: 26px 0 14px;
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 14px;
      margin: 20px 0 15px;
    }
  }

  p {
    font-size: 14px;
    font-weight: 400;
    line-height: 22px;
    margin: 0;

    @include vp-1199 {
      font-size: 14px;
      line-height: 22px;
    }

    @include vp-767 {
      font-size: 12px;
      line-height: 18px;
    }
  }
}

.features__slider-navigation {
  justify-content: center;

  @include vp-1199 {
    display: none;
  }
}

.features__br {
  display: none;

  @include vp-767 {
    display: block;
  }
}
